<template>
  <div class="detalis">
    <div class="head">
      <van-nav-bar title="详情" left-text="返回" left-arrow @click-left="$router.go(-1)" />
    </div>
    <div class="banner">
      <img :src="detalisData.large_img" class="img_auto" />
    </div>

    <div class="content">
      <div class="div">
        <div class="text clearfix">
          <h2 class="fl leftspan">
            {{detalisData.name}}
            <span class="fr price-num">
              <font>￥</font>
              {{detalisData.price}}
            </span>
          </h2>
          <span class="fr span">
            <img
              class="img_auto"
              :src="require('../assets/images/' + (isLike ? 'like_active.png' : 'like.png'))"
              alt
              @click="likeGoods"
            />
          </span>
        </div>
        <van-divider />
        <div class="rule" v-for="(item,index) in detalisData.rules" :key="index">
          <div class="fl clearfix wen">
            <h3>{{item.title}}</h3>
          </div>
          <div class="tem">
            <div
              class="fl clearfix item-choose"
              v-for="(v,i) in item.tag"
              :key="i"
              @click="toggleTag(item,v)"
              :class="{active: v.isActive}"
            >{{v.name}}</div>
          </div>
        </div>
        <van-divider />

        <div class="price clearfix">
          <span class="fl num">购买数量</span>
          <span class="fr">
            <van-stepper v-model="count" theme="round" button-size="22" disable-input />
          </span>
        </div>
        <van-divider />
        <div class="desc">
          <div class="desc-title">
            <h3>商品描述</h3>
          </div>
          <div class="desc-content">
            <div v-for="index in detalisData.desc" :key="index">{{index}}</div>
          </div>
        </div>
        <van-divider />
      </div>
      <div class="car-box">
        <van-goods-action>
          <!-- <van-goods-action-icon icon="chat-o" text="客服" color="#07c160" /> -->
          <van-goods-action-icon
            icon="cart-o"
            text="购物车"
            :badge="shopcartRows == 0 ? '' : shopcartRows"
            @click="goCar"
          />
          <van-goods-action-icon :icon="isLike?'star':'star-o'" text="已收藏" color="#ff5000" />
          <van-goods-action-button type="warning" text="加入购物车" @click="addCar" />
          <van-goods-action-button type="danger" text="立即购买" @click="addCar(true)" />
        </van-goods-action>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      count: 0,
      detalisData: [],
      isLike: false,
      pid: "",
      shopcartRows: 0
    };
  },

  created() {
    //获取pid
    this.pid = this.$route.query.pid;
    //根据pid获取到详情数据
    this.getcontent(this.pid);
    // this.likeGoods()
    this.findLike(this.pid);
    this.getCarnum();
  },
  
  methods: {
    goPay() {
      let tokenString = localStorage.getItem("__jigng");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status: 0
        }
      })
        .then(res => {
          this.$toast.clear();
          
        })
        .catch(err => {
          
        });
    },
    getcontent(pid) {
      this.axios({
        method: "get",
        url: "/productDetail",
        params: {
          appkey: this.appkey,
          pid
        }
      })
        .then(res => {
          
          if (res.data.code == 600) {
            let data = res.data.result[0];
            data.desc = data.desc.split(/\n/);

            let productType = ["tem", "sugar", "milk", "cream"];
            let rules = [];
            productType.forEach(v => {
              if (data[v] != "") {
                let rule = {
                  title: data[v + "_desc"],
                  tag: []
                };
                let tag = data[v].split(/\//);
                tag.forEach((item, i) => {
                  let t = {
                    name: item,
                    isActive: i == 0
                  };
                  rule.tag.push(t);
                });
                rules.push(rule);
                
              }
            });
            data.rules = rules;

            this.detalisData = data;
            
          }
        })
        .catch(err => {
          
        });
    },
    //切换商品规格
    toggleTag(item, v) {
      
      if (v.isActive) {
        return;
      }
      //查找已经激活的规格标签
      for (let i = 0; i < item.tag.length; i++) {
        if (item.tag[i].isActive) {
          item.tag[i].isActive = false;
          break;
        }
      }
      v.isActive = true;
    },
    //收藏商品
    likeGoods() {
      let tokenString = localStorage.getItem("__jigng");
      
      //判断是否收藏，切换接口路径
      let url = this.isLike ? "/notlike" : "/like";

      this.axios({
        method: "POST",
        url,
        data: {
          appkey: this.appkey,
          pid: this.pid,
          tokenString
        }
      })
        .then(result => {
          
          if (result.data.code == 800 || result.data.code == 900) {
            //收藏成功
            this.isLike = result.data.code == 800;
          } else {
            this.$router.push({ name: "登录" });
          }
        })
        .catch(err => {
          
        });
    },
    // 查询当前商品是否被收藏
    findLike(pid) {
      //获取token
      let tokenString = localStorage.getItem("__jigng");

      this.axios({
        method: "GET",
        url: "/findlike",
        params: {
          appkey: this.appkey,
          pid,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          

          if (result.data.code == 1000) {
            this.isLike = result.data.result.length > 0;
          } else {
            //如果token无效，则跳转到登录页面
            // this.$router.push({name: '登录'});
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    //获取购物车的条数
    getCarnum() {
      let tokenString = localStorage.getItem("__jigng");
      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          

          if (result.data.code == 5000) {
            this.shopcartRows = result.data.result.length;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 加入购物车
    addCar(isBuy) {
      let tokenString = localStorage.getItem("__jigng");
      let rules = this.detalisData.rules;

      let rule = [];
      rules.forEach(v => {
        for (let i = 0; i < v.tag.length; i++) {
          if (v.tag[i].isActive) {
            rule.push(v.tag[i].name);
            break;
          }
        }
      });

      
      this.axios({
        method: "POST",
        url: "/addShopcart",
        data: {
          appkey: this.appkey,
          tokenString,

          //商品id
          pid: this.pid,
          //商品数量
          count: this.count,
          //商品规格
          rule: rule.join("/")
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 3000) {
            if (result.data.status == 1) {
              this.shopcartRows++;
            }
            this.$toast(result.data.msg);
            if (isBuy===true) {
              setTimeout(() => {
                this.$router.push({
                  name: "支付",
                  query: { sids: result.data.sid }
                });
              }, 500);
            }
          } else {
            this.$router.push({ name: "登录" });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    goCar() {
      this.$router.push({ name: "购物车" });
    }
  }
};
</script>

<style lang="less" scoped>
.detalis {
    position: fixed;
    top: 46px;
    bottom: 0px;
    left: 0;
    width: 100%;
    // padding-bottom: 200px;
    overflow-y: auto;
    .banner {
      img {
        border-radius: 15px;
      }
    }
    .content {
      border-radius: 15px;
      background-color: white;
      width: 100%;
      position: absolute;
      top: 260px;
      margin-bottom: 60px;
    }
    .text {
      // margin-top: 10px;
      height: 30px;
  
      .leftspan {
        margin: 0;
        padding: 0;
        margin-left: 35px;
        margin-top: 15px;
      }
      .span {
        margin-right: 15px;
        width: 25px;
        margin-top: 12.5px;
      }
      .price-num {
        margin-top: 10px;
        margin-left: 40px;
        color: red;
        font {
          font-size: 12px;
        }
      }
    }
    .rule {
      margin-top: 10px;
      margin-left: 10px;
      height: 30px;
      line-height: 30px;
      .wen {
        text-align: center;
        height: 30px;
        line-height: 30px;
        h3 {
          width: 40px;
        }
      }
      .item-choose {
        width: 75px;
        text-align: center;
        height: 30px;
        line-height: 30px;
        background-color: #f8f8f8;
        border-radius: 10px;
        margin-left: 10px;
        &.active {
          background-color: #3e98f7;
          color: #fff;
        }
      }
    }
  }
  .desc-title {
    width: 90%;
    margin: 0 auto;
  }
  .desc-content {
    font-size: 14px;
    width: 90%;
    margin: 0 auto;
    color: #323233;
  }
  h3 {
    margin: 0;
    padding: 0;
  }
  .price {
    width: 90%;
    margin: 10px auto;
    font-size: 14px;
    height: 30px;
    line-height: 30px;
    .num {
      font-weight: 700;
    }
  }
  
  .van-nav-bar {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
  }
</style>